import React from 'react';
import { motion, useScroll, useTransform } from 'framer-motion';
import { useLanguage } from '../contexts/LanguageContext';

const About = () => {
  const { t } = useLanguage();
  const { scrollY } = useScroll();
  const y1 = useTransform(scrollY, [0, 1000], [0, -100]);
  const y2 = useTransform(scrollY, [0, 1000], [0, -50]);
  const opacity = useTransform(scrollY, [0, 300, 600], [0, 1, 1]);

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.3,
        delayChildren: 0.2,
      },
    },
  };

  const itemVariants = {
    hidden: { opacity: 0, y: 50, scale: 0.9 },
    visible: {
      opacity: 1,
      y: 0,
      scale: 1,
      transition: {
        duration: 0.8,
        ease: "easeOut",
      },
    },
  };

  return (
    <motion.section 
      style={{ opacity }}
      className="relative py-20 px-4 overflow-hidden"
    >
      {/* Enhanced background elements */}
      <div className="absolute inset-0 bg-gradient-to-b from-dark-bg to-dark-surface" />
      
      {/* Parallax background layers */}
      <motion.div 
        style={{ y: y1 }}
        className="absolute inset-0"
      >
        <div className="absolute inset-0 bg-gradient-to-tr from-neon-blue/5 via-transparent to-neon-violet/5" />
      </motion.div>
      
      {/* Enhanced orbiting particles */}
      <motion.div 
        style={{ y: y2 }}
        className="absolute inset-0"
      >
        <motion.div
          animate={{ 
            rotate: 360,
            scale: [1, 1.2, 1],
            opacity: [0.3, 0.8, 0.3]
          }}
          transition={{ duration: 20, repeat: Infinity, ease: "linear" }}
          className="absolute top-1/4 left-1/4 w-4 h-4 bg-neon-blue rounded-full neon-glow-blue"
        />
        <motion.div
          animate={{ 
            rotate: -360,
            scale: [1, 0.8, 1],
            opacity: [0.4, 0.9, 0.4]
          }}
          transition={{ duration: 25, repeat: Infinity, ease: "linear" }}
          className="absolute top-3/4 right-1/4 w-6 h-6 bg-neon-violet rounded-full neon-glow-violet"
        />
        <motion.div
          animate={{ 
            rotate: 360,
            scale: [1, 1.5, 1],
            opacity: [0.2, 0.7, 0.2]
          }}
          transition={{ duration: 30, repeat: Infinity, ease: "linear" }}
          className="absolute top-1/2 right-1/3 w-2 h-2 bg-neon-magenta rounded-full neon-glow-magenta"
        />
        <motion.div
          animate={{ 
            rotate: -180,
            scale: [1, 1.3, 1],
            opacity: [0.3, 0.6, 0.3]
          }}
          transition={{ duration: 15, repeat: Infinity, ease: "linear" }}
          className="absolute bottom-1/3 left-1/3 w-3 h-3 bg-neon-cyan rounded-full neon-glow-cyan"
        />
      </motion.div>

      <div className="relative z-10 max-w-6xl mx-auto">
        <motion.div
          variants={containerVariants}
          initial="hidden"
          whileInView="visible"
          viewport={{ once: true, amount: 0.3 }}
          className="text-center"
        >
          {/* Section title */}
          <motion.div variants={itemVariants} className="mb-16">
            <h2 className="text-4xl md:text-6xl font-bold gradient-text mb-4">
              {t('about.title')}
            </h2>
            <div className="w-24 h-1 bg-gradient-to-r from-neon-blue to-neon-violet mx-auto rounded-full" />
          </motion.div>

          {/* Mission statement */}
          <motion.div variants={itemVariants} className="mb-12">
            <p className="text-xl md:text-2xl text-gray-300 leading-relaxed max-w-4xl mx-auto">
              {t('about.mission')}
            </p>
          </motion.div>

          {/* Features grid */}
          <motion.div 
            variants={containerVariants}
            className="grid md:grid-cols-3 gap-8 mt-16"
          >
            {[
              {
                icon: "ðŸš€",
                title: t('about.features.innovation.title'),
                description: t('about.features.innovation.description')
              },
              {
                icon: "ðŸŽ®",
                title: t('about.features.fun.title'),
                description: t('about.features.fun.description')
              },
              {
                icon: "âš¡",
                title: t('about.features.growth.title'),
                description: t('about.features.growth.description')
              }
            ].map((feature, index) => (
              <motion.div
                key={index}
                variants={itemVariants}
                whileHover={{ 
                  scale: 1.05,
                  y: -10,
                  transition: { duration: 0.3 }
                }}
                className="glass rounded-2xl p-8 hover-lift group"
              >
                <div className="text-4xl mb-4 group-hover:scale-110 transition-transform duration-300">
                  {feature.icon}
                </div>
                <h3 className="text-xl font-semibold text-neon-cyan mb-4">
                  {feature.title}
                </h3>
                <p className="text-gray-400 leading-relaxed">
                  {feature.description}
                </p>
              </motion.div>
            ))}
          </motion.div>

          {/* Stats */}
          <motion.div 
            variants={itemVariants}
            className="mt-20 grid grid-cols-2 md:grid-cols-4 gap-8"
          >
            {[
              { number: "3+", label: t('about.stats.apps') },
              { number: "1M+", label: t('about.stats.downloads') },
              { number: "50+", label: t('about.stats.countries') },
              { number: "24/7", label: t('about.stats.innovation') }
            ].map((stat, index) => (
              <motion.div
                key={index}
                whileHover={{ scale: 1.1 }}
                className="text-center"
              >
                <div className="text-3xl md:text-4xl font-bold gradient-text mb-2">
                  {stat.number}
                </div>
                <div className="text-sm text-gray-400 uppercase tracking-wider">
                  {stat.label}
                </div>
              </motion.div>
            ))}
          </motion.div>
        </motion.div>
      </div>
    </motion.section>
  );
};

export default About;
